<template>
  <div style="padding:0 0 30px 0">
    <loading :active.sync="isLoading"></loading>
    <div class="container">
      <div class="row justify-content-md-between">
        <div class="col-md-7">
          <!-- cart -->
          <div class="row mt-5 mb-5">
            <table class="table">
              <thead>
                <tr>
                  <th scope="col" width="50"></th>
                  <th scope="col"></th>
                  <th scope="col"></th>
                  <th scope="col" class="text-right"></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item) in carts" :key="item.id">
                  <th scope="row">
                    <button
                      type="button"
                      class="btn btn-outline-danger"
                      @click="deleteCart(item.id)"
                    >
                      <i class="fa fa-trash" aria-hidden="true"></i>
                    </button>
                  </th>
                  <td>{{item.product.title}}</td>
                  <td>{{item.qty}} {{item.product.unit}}</td>
                  <td class="text-right">{{item.product.price | currency}} </td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="3" class="text-right"></td>
                  <td class="text-right">{{total | currency}} </td>
                </tr>
                <tr v-if="total!==final_total">
                  <td colspan="3" class="text-right text-success"></td>
                  <td class="text-right">{{final_total | currency}} </td>
                </tr>
              </tfoot>
            </table>
          </div>

          <!--  -->
          <div class="row justify-content-center mt-5 mb-5 no-gutters">
            <div class="input-group mb-3 input-group-sm col-lg-5">
              <input type="text" class="form-control" v-model="coupon_code" placeholder="" />
              <div class="input-group-append">
                <button class="btn btn-outline-secondary" type="button" @click="addCouponCode"></button>
              </div>
            </div>
          </div>
        </div>
        <div class="col-md-4">
          <!--  -->

          <div class="mt-5 justify-content-lg-end no-gutters">
            <ValidationObserver ref="form">
              <form @submit.prevent="onSubmit">
                <div class="form-group">
                  <ValidationProvider name="email" rules="required|email" v-slot="{ errors }">
                    <label for="useremail">Email</label>
                    <input
                      type="email"
                      class="form-control"
                      :class="{'is-invalid':errors[0]}"
                      name="email"
                      id="useremail"
                      v-model="form.user.email"
                      placeholder=" Email"
                    />
                    <span class="text-danger" v-if="errors[0]">{{errors[0]}}</span>
                  </ValidationProvider>
                </div>

                <div class="form-group">
                  <ValidationProvider name="name" rules="required" v-slot="{ errors }">
                    <label for="username"></label>
                    <input
                      type="text"
                      class="form-control"
                      :class="{'is-invalid':errors[0]}"
                      name="First Name"
                      id="username"
                      v-model="form.user.name"
                      placeholder=""
                    />
                    <span class="text-danger" v-if="errors[0]">{{errors[0]}}</span>
                  </ValidationProvider>
                </div>

                <div class="form-group">
                  <ValidationProvider name="tel" rules="required" v-slot="{ errors }">
                    <label for="usertel"></label>
                    <input
                      type="tel"
                      class="form-control"
                      :class="{'is-invalid':errors[0]}"
                      id="usertel"
                      v-model="form.user.tel"
                      placeholder=""
                    />
                    <span class="text-danger" v-if="errors[0]"></span>
                  </ValidationProvider>
                </div>

                <div class="form-group">
                  <ValidationProvider name="address" rules="required" v-slot="{ errors }">
                    <label for="useraddress"></label>
                    <input
                      type="text"
                      class="form-control"
                      name="address"
                      :class="{'is-invalid':errors[0]}"
                      id="useraddress"
                      v-model="form.user.address"
                      placeholder=""
                    />
                    <span class="text-danger"></span>
                  </ValidationProvider>
                </div>

                <div class="form-group">
                  <label for="comment"></label>
                  <textarea
                    name
                    id="comment"
                    class="form-control"
                    cols="30"
                    rows="10"
                    v-model="form.message"
                  ></textarea>
                </div>
                <button type="reset" class="btn btn-info"></button>
                <button type="submit" class="btn btn-danger"></button>
              </form>
            </ValidationObserver>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>



<script>
export default {
  data() {
    return {
      products: [],
      product: {},
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
        },
        message: "",
      },
      coupon_code: "",
      status: { lodingItem: "" },
      final_total: {},
      total: {},
      pagination: {},
      carts: [],
      tempProduct: {},
      isNew: false,

      status: { fileUpLoading: false },
    };
  },
  methods: {
    getProducts() {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`; // const api = "https://vue-course-api.hexschool.io/api/redbs/products/all";
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.isLoading = false;
        vm.products = response.data.products;
        console.log("response", response.data);
        // vm.pagination = response.data.pagination;
        console.log("5678", vm.products);
      });
    },
    getProuduct(id) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`; // const api = "https://vue-course-api.hexschool.io/api/redbs/products/all";
      const vm = this;

      vm.status.lodingItem = id;
      this.$http.get(api).then((response) => {
        vm.product = response.data.product;
        $("#productModal").modal("show");
        console.log("response", response.data);
        // vm.pagination = response.data.pagination;
        console.log("", vm.product);
        vm.status.lodingItem = "";
      });
    },
    addToCart(id, qty = 1) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`; // const api = "https://vue-course-api.hexschool.io/api/redbs/products/all";
      const vm = this;
      vm.status.lodingItem = id;
      const cart = { product_id: id, qty };
      this.$http.post(api, { data: cart }).then((response) => {
        console.log("response", response.data);
        vm.getCart();
        $("#productModal").modal("hide");
        vm.status.lodingItem = "";
      });
    },
    getCart() {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`; // const api = "https://vue-course-api.hexschool.io/api/redbs/products/all";
      const vm = this;

      this.$http.get(api).then((response) => {
        vm.carts = response.data.data.carts;
        vm.final_total = response.data.data.final_total;
        vm.total = response.data.data.total;
        console.log("", response);
        // console.log("vm", vm.carts);
      });
    },
    deleteCart(id) {
      this.$store.dispatch("updateLoading", true);
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`; // const api = "https://vue-course-api.hexschool.io/api/redbs/products/all";
      const vm = this;

      this.$http.delete(api).then((response) => {
        vm.getCart();
        this.$store.dispatch("getCart");
        // console.log("", response);
        this.$store.dispatch("updateLoading", false);
      });
    },
    addCouponCode() {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`; // const api = "https://vue-course-api.hexschool.io/api/redbs/products/all";
      const vm = this;
      const coupon = { code: vm.coupon_code };
      this.$http.post(api, { data: coupon }).then((response) => {
        vm.getCart();
        console.log("", response);
      });
    },
    creatorOrder() {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`; // const api = "https://vue-course-api.hexschool.io/api/redbs/products/all";
      const vm = this;
      const order = vm.form;
      this.$http.post(api, { data: order }).then((response) => {
        console.log("", response);
        if (response.data.success) {
          vm.$router.push(`/customer_check_out/${response.data.orderId}`);
        }
      });
    },
    onSubmit() {
      this.$refs.form.validate().then((success) => {
        if (success) {
          //  AJAX
          this.creatorOrder();
        } else {
          // 
          console.log("");
        }
      });
    },
  },

  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
  },

  created() {
    this.getProducts();
    this.getCart();
  },
};
</script>

<style lang="scss" scoped>
</style>